import React, { useState, useEffect } from "react";
import { Link, location } from "react-router-dom";
import "./Header.css";

const Header = ({ toggleNavbar, isNavVisible, setShowLoginModal}) => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [userData, setUserData] = useState(null);
  const [userStatus, setUserStatus] = useState("");

  useEffect(() => {
    //    
    const storedUser = JSON.parse(localStorage.getItem("user"));
    if (storedUser) {
      setUserData(storedUser.user); //   
      setIsLoggedIn(true);
      if (storedUser.user.status === "a") {
        setUserStatus("");
      } else if (storedUser.user.status === "m") {
        setUserStatus("");
      } else {
        setUserStatus("");
      }
    } else {
      setIsLoggedIn(false);
    }
  }, []);

  const handleLogout = () => {
    localStorage.clear();
    setIsLoggedIn(false);
    setUserData(null); //    
  };

  return (
    <header className="header">
      <h1 className="header-title">
        <Link to="/" className="Header-button">HANCHEOL ERP</Link>
      </h1>

      {/*      */}
      <div className="login-section">
        {isLoggedIn ? (
          <div className="loginState">
            <p>, {userData?.userName}! ({userStatus})</p>
            <button className="header-button" onClick={handleLogout}></button>
          </div>
        ) : (
          <div className="loginState">
            <button className="header-button" onClick={() => setShowLoginModal(true)}></button>
            <Link to="/Signup" style={{ textDecoration: 'none' }}>
              <button className="header-button">
                
              </button>
            </Link>
          </div>
        )}
      </div>

      <button className="navbar-toggle header-button" onClick={toggleNavbar}>
        {isNavVisible ? "" : ""}
      </button>
    </header>
  );
};

export default Header;
